import { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";

// isLazy chuyn t true sang false v ngc li trong vi ms dn n component loadding hiu lm l  completed loadding v chuyn sang hiu ng fadeout
// Hook ny c tc dng nu isLazy chuyn i t true > false th phi cch ln chuyn trc 100ms mi cho php chuyn
export default function UseHandleVibrateLazy() {
  const { isLazy } = useSelector((state) => state.lazyReducer);
  const clear = useRef(null);
  const [isLazyout, setisLazyout] = useState(false);
  useEffect(() => {
    if (isLazy) {
      setisLazyout(true);
      clearTimeout(clear.current);
    } else {
      clear.current = setTimeout(() => {
        setisLazyout(false); // ch 100ms sau mi chuyn sang false, nhng nu cha ti 100ms m isLazy chuyn thnh true th hy set false
      }, 100);
    }
  }, [isLazy]);
  return isLazyout;
}
