/*
- isLazy: khi Suspense(trong App) return v component th isLazy = true, khi Suspense unmount component th isLazy = false,
    do boolean isLazy toggle qu nhanh nn dng useHandleVibrateLazy  hn ch reloading loading nhiu ln
- c 3 kiu bin dng  hin loading: 1 l isLazy(component  ln u loading), 2 l call api trong component, 3 l isLoadingBackToHome > do qu trnh render homepage rt lu.
- dng useRef  lu li gi tr trc  ca loading, khi loading thay i ta bit c loading chuyn trng thi t true > false hay ngc li t  kim sot hiu ng:
    cc bin kim sot hiu ng:
              loadingStart    loadingCompleted
    visible   true            delay 600ms > false
    fadein    true            false
    fadeout   false           true
- mun kch hot li mt keyframes phi i gi tr key(khai nim trong react), hoc move class cha keyframes ra v thm vo li
- mi ln quay v home v mt mt thi gian mi return component(mc d  ti sn code + data) >  to loading th:
  + khi click th dispatch isLoadingBackToHome l true trong reducer Lazy  kch hot loading
  + bn trong component page > Homepage > Carousel kch hot loading xong nu component render xong
- do cng mt element khng th c 2 animation cng lc ln phi tch ra thnh 2 div, 1 div shake, mt div fade
- khi fadeOut kt thc th cn n i loadding > bt s kin animationend, sau  xa addEventListener  khng bt animationend ca fadeIn

*/
import React, { useEffect, useRef, useState, useCallback } from "react";

import { makeStyles } from "@material-ui/core";
import clsx from "clsx";
import { useSelector } from "react-redux";
import { useTheme } from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";

import { IMG_LOADING } from "../../constants/config";
import useHandleVibrateLazy from "../../utilities/useHandleVibrateLazy";
import SpinnerLoading from "../SpinnerLoading/SpinnerLoading";

const useStyles = makeStyles({
  root: {
    position: "fixed",
    top: 0,
    left: 0,
    backgroundColor: (props) => (props.effectFadeOut ? "transparent" : "#000"),
    zIndex: -1,
    transition: "background-color 0.6s ease-in-out",

    width: "100vw",
    height: "100vh",
    justifyContent: "center",
    alignItems: "center",
  },
  visible: {
    zIndex: 99999,
  },
  image: {
    width: (props) => (props.isDesktop ? 250 : 100),
    animation: "$shake 0.6s infinite",
    position: "relative",
  },
  fadeIn: {
    animationName: "$fadeIn",
    animationDuration: "0.6s",
  },
  fadeOut: {
    animationName: "$fadeOut",
    animationDuration: "0.6s",
  },
  "@keyframes shake": {
    "0%": { transform: "rotate(-10deg)" },
    "50%": { transform: "rotate(10deg)" },
    "100%": { transform: "rotate(-10deg)" },
  },
  "@keyframes fadeIn": {
    "0%": { opacity: 0, transform: "scale(0.6,0.6)" },
    "70%": { opacity: 0.7, transform: "scale(1.1,1.1)" },
    "100%": { opacity: 1, transform: "scale(1)" },
  },
  "@keyframes fadeOut": {
    "0%": { opacity: 1, transform: "scale(1)" },
    "70%": { opacity: 0.7, transform: "scale(1.1,1.1)" },
    "100%": { opacity: 0, transform: "scale(0.6,0.6)" },
  },
});

export default function Loading() {
  const isLazy = useHandleVibrateLazy();
  const isLoadingBackToHome = useSelector(
    (state) => state.lazyReducer.isLoadingBackToHome
  );
  const loadingMovieList = useSelector(
    (state) => state.movieReducer.loadingMovieList
  );
  const loadingGetListSeat = useSelector(
    (state) => state.bookTicketReducer.loadingGetListSeat
  );
  const loadingMovieDetailShowtimes = useSelector(
    (state) => state.movieDetailReducer.loadingMovieDetailShowtimes
  );
  const loading =
    isLazy ||
    loadingMovieList ||
    loadingGetListSeat ||
    loadingMovieDetailShowtimes ||
    isLoadingBackToHome;
  const loadingPrevious = useRef(false);
  const theme = useTheme();
  const isDesktop = useMediaQuery(theme.breakpoints.up("md"));

  const [controlEffect, setControlEffect] = useState({
    visible: false,
    effectFadeIn: false,
    effectFadeOut: false,
  });
  const eFadeEffect = useRef(null);
  const materialStyles = useStyles({
    isDesktop,
    loading,
    visible: controlEffect.visible,
    effectFadeOut: controlEffect.effectFadeOut,
  });

  useEffect(() => {
    // loadding chuyn t false sang true
    if (Number(loadingPrevious.current) < Number(loading)) {
      // console.log("START: ", loadingPrevious.current, loading);
      setControlEffect((data) => ({
        ...data,
        visible: true,
        effectFadeIn: true,
        effectFadeOut: false,
      }));
      loadingPrevious.current = true;
      // loadding chuyn t true sang false
    } else if (Number(loadingPrevious.current) > Number(loading)) {
      // console.log("END: ", loadingPrevious.current, loading);
      setControlEffect((data) => ({
        ...data,
        visible: true,
        effectFadeIn: false,
        effectFadeOut: true,
      }));
      loadingPrevious.current = false;
      // khi fadeOut kt thc th reset loading
      eFadeEffect.current?.addEventListener("animationend", resetAnimation);
    }
  }, [loading]);

  const resetAnimation = useCallback((e) => {
    // dng useCallback v removeEventListener ch xa s kin da trn cng mt function
    eFadeEffect.current?.removeEventListener("animationend", resetAnimation);
    setControlEffect((data) => ({
      ...data,
      visible: false,
      effectFadeIn: false,
      effectFadeOut: false,
    }));
  }, []);

  return (
    <div
      className={clsx(
        `${materialStyles.root}`,
        controlEffect.visible && `${materialStyles.visible}`
      )}
      // khi chuyn url > component mi cha load xong nn loading(zIndex: -1) hin ra > cn n i lc   hiu ng fadeIn mt hn
      style={{ display: controlEffect.visible ? "flex" : "none" }}
    >
      <div
        ref={eFadeEffect}
        className={clsx(
          controlEffect.effectFadeIn && `${materialStyles.fadeIn}`,
          controlEffect.effectFadeOut && `${materialStyles.fadeOut}`
        )}
      >
        {/* <img src={IMG_LOADING} className={materialStyles.image} alt="logo" /> */}
        <SpinnerLoading/>
      </div>
    </div>
  );
}

